import React, { useEffect, useState, ReactNode, useRef } from 'react';
import Box from '@mui/material/Box';

type FadeInProps = {
  children: ReactNode;
};

const FadeIn = (props: FadeInProps) => {
    const [isVisible, setIsVisible] = useState(false);
    const ref = useRef<HTMLDivElement>(null);
  
    useEffect(() => {
      const observer = new IntersectionObserver(
        ([entry]) => {
          // Adjust the threshold and rootMargin to control when the animation triggers
          setIsVisible(entry.isIntersecting);
        },
        {
          root: null,
          rootMargin: '0px',
          threshold: 0.5, // Adjust threshold to 0.5 for middle of the viewport
        }
      );
  
      if (ref.current) {
        observer.observe(ref.current);
      }
  
      return () => {
        if (ref.current) {
          observer.unobserve(ref.current);
        }
      };
    }, [ref]);
  
    return (
      <Box
        ref={ref}
        sx={{
          opacity: isVisible ? 1 : 0,
          transform: isVisible ? 'scale(1)' : 'scale(0.2)',
          transition: 'opacity 0.3s cubic-bezier(0.47, 0, 0.745, 0.715), transform 0.3s cubic-bezier(0.47, 0, 0.745, 0.715)',
          // Add additional styling as needed
        }}
      >
        {props.children}
      </Box>
    );
  };
  
  export default FadeIn;