type timelineInfoProps = {
  items: string[];
  header: string;
  subheader: string;
};

export const Roadmap = (props: timelineInfoProps) => {
  return (
    <Box
      display="flex"
      flexDirection="column"
      maxWidth={"lg"}
      padding={3}>
      <center>
        <Typography variant="h4" fontWeight="bold" color={"white"}>
          {props.header}
        </Typography>
      </center>
      <center>
        <Typography variant="h6" fontWeight="bold" color={"white"}>
          {props.subheader}
        </Typography>
      </center>
      <List>
        {props.items.map((item, index) => {
          return (
            <ListItem>
              <RadioButtonUncheckedIcon sx={{color: "white", paddingRight: 2}}/>
              
              <Typography variant="body1"  color="white">
                {item}
              </Typography>
            </ListItem>
          );
        })}
      </List>
    </Box>
  );
};
